.dialog_wrapper {
	display: grid;
	grid-template-areas: "dialogs messages";
	grid-template-columns: 30% 70%;
	padding: 20px 40px;
	height: 100%;
}

.dialogs {
	grid-area: dialogs;
	border-right: 1px dashed tomato;
}

.dialogs_title {
	text-transform: uppercase;
	font-weight: 600;
	color: teal;
	margin-bottom: 40px;
}

.messages {
	grid-area: messages;
	padding-left: 40px;
	margin-top: 55px;
}

.newMessage button {
	width: 70px;
	height: 30px;
	background-color: teal;
	color: #fff;
	cursor: pointer;
	border-radius: 3px;
	margin-top: 6px;
	display: block;
	margin-left: auto;
	margin-right: 5.5%;
}
.newMessage button:hover {
	color: #ccc;
	border-right: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}
.newMessage button:active {
	color: tomato;
	border-right: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}
