.message {
	padding-bottom: 20px;
	display: grid;
	grid-template-areas: "avatar cont";
	grid-template-columns: 50px 1fr;
}

.avatar {
	grid-area: avatar;
	width: 40px;
	height: 40px;
	border-radius: 50%;
}

.content {
	grid-area: cont;
}

.message .date {
	font-size: 11px;
	color: rgb(120, 120, 120);
}

.message .name {
	font-size: 13px;
	font-weight: 600;
	color: teal;
	margin-right: 8px;
}

.message p {
	margin-top: 5px;
}
